span,h6{
	font-size:14pt!important;
}

body{
	//background:#f1f1f1;
}

svg{
	width:20px;
	color:#44bcd8;
}

@font-face {
	font-display: swap;
}

.badge_custom{
	background-color:rgba(61, 92, 130, 0.9);
	float:right;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bg_own{
	background-color:rgba(61, 92, 130, 0.9);
	color:#fff;
	font-weight:bold;
}

.badge-primary_own{
	background-color:#00458F;
	color:#fff;
}

.badge-info_own{
	background-color:#545454;
	color:#fff;
}

.badge-warning_own{
	background-color:#A20740;
}

.fa{
	padding:14px;
}

img{
	border: 3px solid;
    border-radius: 12px;	
}

.add-to-btn{
	//background-color :rgba(61, 92, 130, 0.9);
	//border-color: rgba(61, 92, 130, 0.9);
	color:#fff;
	font-weight:bold;
}

.add-to-btn:hover{
	background-color :rgba(61, 92, 130, 0.9);
	color:#fff;
	font-weight:bold;
	transition:2s;
}

.add-to-btn:focus{
	box-shadow:unset;
}

.add-to-btn i{
	font-weight:bold;
}

i.fa.fa-plus-square-o {
    padding: inherit;
}

.social_links a{
	color:#fff;
	font-size:28px;
}

.card{
	border:unset;
	background:unset;
}

span.list-group-item.list-group-item-action{
	border-radius:unset;
}

img{
	width:10em;
}

@media only screen and (max-width: 900px) {
	
	button{
		width:100%;
	}	
}

.card-header:first-child{
	border-top-right-radius:5px;
	border-top-left-radius:5px;
}

.card-header{
	border-top-right-radius:5px;
	border-top-left-radius:5px;
}

footer , .banner{
	border-radius:5px;
}
